.footer {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  gap: 1.2rem;
  margin-top: auto;
  /* position: fixed;
  left: 0;
  bottom: 0; */
  padding-bottom: 1.5rem;
  width: 100%;
  background-color: transparent;
  color: #4f4f4f;
  text-align: center;
}

.cta {
  text-transform: uppercase;
}

.socialLinks {
  display: flex;
  align-items: center;
  gap: 0.6rem;
}

.socialLinks a {
  height: 40px;
}

.socialIconLink {
  width: 40px;
  height: 40px;
}

.copyright {
  font-size: 0.8rem;
}

@media all and (max-width: 420px) {
  .cta {
    font-size: 0.8rem;
  }

  .socialLinks {
    gap: 0.6rem;
  }

  .socialLinks a {
    height: 30px;
  }

  .socialIconLink {
    width: 30px;
    height: 30px;
  }

  .copyright {
    font-size: 0.6rem;
  }
}
